import React, {ChangeEvent, useState, KeyboardEvent} from 'react'
import Greeting from './Greeting'
import {UserType} from "./HW3";


type GreetingContainerPropsType = {
    users: UserType[], // need to fix any
    addUserCallback: (name: string) => void // need to fix any
}

// –±–æ–ª–µ–µ –ø—Ä–æ—Å—Ç–æ–π –∏ –ø–æ–Ω—è—Ç–Ω—ã–π –¥–ª—è –Ω–æ–≤–∏—á–∫–æ–≤
// function GreetingContainer(props: GreetingPropsType) {

// –±–æ–ª–µ–µ —Å–æ–≤—Ä–µ–º–µ–Ω–Ω—ã–π –∏ —É–¥–æ–±–Ω—ã–π –¥–ª—è –ø—Ä–æ :)
// —É—Ä–æ–≤–µ–Ω—å –ª–æ–∫–∞–ª—å–Ω–æ–π –ª–æ–≥–∏–∫–∏
const GreetingContainer: React.FC<GreetingContainerPropsType> = ({users, addUserCallback}) => { // –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—è –ø—Ä–æ–ø—Å–æ–≤
    const [name, setName] = useState<string>('') // need to fix any
    const [error, setError] = useState<string>('') // need to fix any

    const setNameCallback = (e: ChangeEvent<HTMLInputElement>) => { // need to fix any
        const trimedName = e.currentTarget.value.trim()

        if (trimedName) {
            setName(trimedName)
            setError('')
        } else {
            setName('') // need to fix
            setError('name is require!')
        }
    }
    const addUser = () => {
        addUserCallback(name)
        alert(`Hello your name is: ${name}`)// need to fix
        setName('')
    }

    const onEnter = (e: KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter' && name) {
            addUser()
        }
    }

    const totalUsers = users.length // need to fix

    return (
        <Greeting
            name={name}
            setNameCallback={setNameCallback}
            addUser={addUser}
            onEnter={onEnter}
            error={error}
            totalUsers={totalUsers}
        />
    )
}

export default GreetingContainer
