import React, {ChangeEvent, useState} from 'react'
import SuperInputText from './common/c1-SuperInputText/SuperInputText'
import s from './HW4.module.css'
import SuperButton from './common/c2-SuperButton/SuperButton'
import SuperCheckbox from './common/c3-SuperCheckbox/SuperCheckbox'

function HW4() {
    const [text, setText] = useState<string>('')
    const error = text ? '' : 'error'

    const showAlert = () => {
        if (error) {
            alert('–≤–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç...')
        } else {
            alert(text) // –µ—Å–ª–∏ –Ω–µ—Ç –æ—à–∏–±–∫–∏ –ø–æ–∫–∞–∑–∞—Ç—å —Ç–µ–∫—Å—Ç
        }
    }

    const [checked, setChecked] = useState<boolean>(false)
    const testOnChange = (e: ChangeEvent<HTMLInputElement>) => setChecked(e.currentTarget.checked)

    return (
        <div>
            <hr/>
            homeworks 4

            <div className={s.column}>

                <div>
                    <SuperInputText
                        value={text}
                        onChangeText={setText}
                        onEnter={showAlert}
                        error={error}
                    />

                    <SuperInputText
                        className={s.blue} // –ø—Ä–æ–≤–µ—Ä—å—Ç–µ, —Ä–∞–±–æ–µ—Ç –ª–∏ —Å–º–µ—à–∏–≤–∞–Ω–∏–µ –∫–ª–∞—Å—Å–æ–≤
                    />
                </div>
                {/*----------------------------------------------------*/}
                <div>
                    <SuperButton>
                        default
                    </SuperButton>

                    <SuperButton
                        red // –ø—Ä–æ–ø—Å—É —Å –±—É–ª–µ–≤—ã–º –∑–Ω–∞—á–µ–Ω–∏–µ–º –Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ —É–∫–∞–∑—ã–≤–∞—Ç—å true
                        onClick={showAlert}
                    >
                        delete {/*// –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ –ø–æ–ø–∞–¥—ë—Ç –≤ children*/}
                    </SuperButton>

                    <SuperButton disabled>
                        disabled
                    </SuperButton>
                </div>
                {/*----------------------------------------------------*/}
                <div>
                    <SuperCheckbox
                        checked={checked}
                        onChangeChecked={setChecked}
                    >
                        some text {/*// —ç—Ç–æ—Ç —Ç–µ–∫—Å—Ç –ø–æ–ø–∞–¥—ë—Ç –≤ children*/}
                    </SuperCheckbox>

                    {/*// onChange —Ç–æ–∂–µ –¥–æ–ª–∂–µ–Ω —Ä–∞–±–æ—Ç–∞—Ç—å*/}
                    <SuperCheckbox checked={checked} onChange={testOnChange}/>
                </div>
            </div>

            <hr/>
            {/*–¥–ª—è –ª–∏—á–Ω–æ–≥–æ —Ç–≤–æ—Ä—á–µ—Å—Ç–≤–∞, –º–æ–≥—É –ø—Ä–æ–≤–µ—Ä–∏—Ç—å*/}
            {/*<AlternativeSuperInputText/>*/}
            {/*<AlternativeSuperButton/>*/}
            {/*<AlternativeSuperCheckbox/>*/}
            <hr/>
        </div>
    )
}

export default HW4
