import React from 'react'
import Affair from './Affair'
import cl from './Affair.module.css'
import {AffairType, FilterType} from './HW2'

type AffairsPropsType = { // need to fix any
    data: AffairType[],
    setFilter: (filter: FilterType) => void,
    deleteAffairCallback: (id: number) => void
}

function Affairs(props: AffairsPropsType) {

    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // –∫–µ–∏ –û–ë–Ø–ó–ê–¢–ï–õ–¨–ù–´ –≤ 99% - —Ç–∞–∫ —á—Ç–æ –ª—É—á—à–µ –∏—Ö –ø–∏—Å–∞—Ç—å –≤—Å–µ–≥–¥–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –≤ –º–∞–ø–µ
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))


    const setAll = () => {
        props.setFilter('all')
    } // need to fix
    const setHigh = () => {
        props.setFilter('high')
    }
    const setMiddle = () => {
        props.setFilter('middle')
    }
    const setLow = () => {
        props.setFilter("low")
    }

    return (
        <div className={cl.affairs}>

            {mappedAffairs}
            <div>
                <button className={cl.button} onClick={setAll}>All</button>
                <button className={cl.button} onClick={setHigh}>High</button>
                <button className={cl.button} onClick={setMiddle}>Middle</button>
                <button className={cl.button} onClick={setLow}>Low</button>
            </div>
        </div>
    )
}

export default Affairs
